<template>
  <div class="panel-wrap">
    <app-header
      title="车辆详情"
      showBack="true"
    ></app-header>
    <div class="panel-main-wrap" id="panelWrap">
      <div id="info">
        <div class="infos">
          <div class="info">
            <span>车架号: <em>{{infos.vhvin || '****'}}</em></span>
            <span>设备号: <em>{{infos.simNo || '****'}}</em></span>
          </div>
          <!-- <div class="icons">
            <div class="icon-item">
              <i class="fa fa-paper-plane" aria-hidden="true"></i>
              <div class="desc">
                <span>里程</span>
                <span>{{infos.totalMileage || '0'}}km</span>
              </div>
            </div>
            <div class="icon-item">
              <i class="fa fa-line-chart" aria-hidden="true"></i>
              <div class="desc">
                <span>车速</span>
                <span>0km</span>
              </div>
            </div>
            <div class="icon-item">
              <i class="fa fa-free-code-camp" aria-hidden="true"></i>
              <div class="desc">
                <span>燃油</span>
                <span>{{infos.totalFuelConsumption || '0'}}L</span>
              </div>
            </div>
          </div> -->
          <div class="line">最后回传时间: {{infos.date || '暂无数据'}}</div>
        </div>
        <div class="btns">
          <router-link :to="'/track/panel/info/' + infos.simNo" class="item">
            <i class="fa fa-truck" aria-hidden="true"></i>
            <span>车辆信息</span>
          </router-link>
          <router-link :to="'/track/panel/report/' + infos.vhcle" class="item">
            <i class="fa fa-file-text-o" aria-hidden="true"></i>
            <span>数据报表</span>
          </router-link>
          <router-link :to="'/track/panel/map/' + infos.vhcle" class="item">
            <i class="fa fa-map" aria-hidden="true"></i>
            <span>行车轨迹</span>
          </router-link>
          <router-link :to="'/track/panel/figure/' + infos.vhcle" class="item">
            <i class="fa fa-area-chart" aria-hidden="true"></i>
            <span>数据可视化</span>
          </router-link>
        </div>
        <div class="address" style="font-size: 13px">
          <i class="fa fa-flag" aria-hidden="true"></i>
          {{infos.address}}
        </div>
      </div>
      <div class="map-wrap" id="mapWrap">
        <div id="bMap" style="height: 100%;"></div>
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
import AppHeader from '../../../../components/header/header.vue'
import {getTrackDatas} from '../../../../api/maintain'
import {initAMap} from '../../../../assets/js/amap'
import {setHeight} from '../../../../lib/util'
export default {
  name: 'Track-Panel',
  data () {
    return {
      infos: '',
      lng: '',
      lat: ''
    }
  },
  created () {
    this.$indicator.open({
      text: '加载中...'
    })
    initAMap()
    this.getDatas()
  },
  beforeRouteLeave (to, from, next) {
    this.$indicator.close()
    next()
  },
  mounted () {
    this.$nextTick(() => {
      document.getElementById('mapWrap').style.height = (setHeight('panelWrap', 'info') - 55) + 'px'
    });
  },
  methods: {
    getDatas () {
      getTrackDatas(this.$route.params.id)
        .then(data => {
          if (data.state) {
            this.$set(this, 'infos', data.data.info)
            this.lng = data.data.info.lon
            this.lat = data.data.info.lat
            this.$indicator.close()
            setTimeout(() => {
              this.createMap ()
            }, 1000)
          } else {
            this.$indicator.close()
            this.$toast(data.errormsg || '网络错误')
          }
        })
        .catch(err => {
          console.error(err)
        })
    },
    createMap () {
      var map = new AMap.Map('mapWrap', {
        resizeEnable: true,
        center: [this.lng, this.lat],
        zoom: 13
      });
      var marker = new AMap.Marker({
        position: map.getCenter(),
        draggable: true,
        cursor: 'move'
      });
      marker.setMap(map);
      // 设置点标记的动画效果，此处为弹跳效果
      marker.setAnimation('AMAP_ANIMATION_BOUNCE');
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .panel-wrap{
    height: 100%;
    background-color: #fff;
    .panel-main-wrap{
      width: 100%;
      height: 100%;
      position: fixed;
      padding-top: 50px;
      overflow: hidden;
      background-color: #fff;
      .infos{
        padding: .6rem;
        padding-top: 0;
        .info{
          padding: .6rem 0;
          border-bottom: 1px solid #eee;
          font-size: 14px;
          font-weight: 500;
          span{
            margin-right: 1rem;
            &:last-child{
              margin-right: 0;
            }
            em {
              font-style: normal;
            }
          }
        }
        .icons{
          display: flex;
          padding: 1rem 0;
          border-bottom: 1px solid #ddd;
          .icon-item{
            flex: 1;
            display: flex;
            text-align: center;
            i{
              display: block;
              flex: 1;
              font-size: 36px;
              color: cornflowerblue;
            }
            .desc{
              flex: 1;
              font-size: 12px;
              span{
                display: block;
              }
            }
          }
        }
        .line{
          padding: .6rem 0;
          border-bottom: 1px solid #ddd;
        }
      }
      .btns{
        display: flex;
        .item{
          flex: 1;
          text-align: center;
          font-size: 14px;
          border-bottom: 1px solid #ddd;
          color: #333;
          i {
            display: block;
            font-size: 36px;
            color:cornflowerblue;
          }
          span{
            display: block;
            padding-top: .3rem;
            padding-bottom: .3rem;
          }
        }
      }
      .address{
        padding: .6rem;
        border-bottom: 1px solid #ddd;
        i{
          font-size: 36px;
          color: cornflowerblue;
          vertical-align: middle;
        }
      }
    }
  }
</style>



